@charset "utf-8";
@import url("https://fonts.googleapis.com/css?family=Roboto:400,500,700&display=swap");
*,
:after,
:before {
  box-sizing: border-box;
}
* {
  margin: 0;
  padding: 0;
  outline: none;
}
html * {
  max-height: 999999px;
}
html,
body {
  height: 100%;
  overflow: hidden;
}
body {
  background-color: #fff;
  color: #353d43;
  font-size: 14px;
  line-height: 24px;
  font-family: -apple-system, BlinkMacSystemFont, "Roboto", Arial, Verdana,
    Tahoma, Helvetica, sans-serif;
}
a {
  text-decoration: none;
  color: #2173af;
}
a:hover {
  text-decoration: none;
}
a img {
  border: none;
}
img {
  max-width: 100%;
  height: auto;
  vertical-align: middle;
}
ul {
  list-style: none;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 700;
  color: #353d43;
}
h1 {
  font-size: 39px;
  line-height: 49px;
}
h2 {
  font-size: 30px;
  line-height: 46px;
}
h3 {
  font-size: 19px;
  line-height: 42px;
}
h4 {
  font-size: 16px;
  line-height: 26px;
}
h5 {
  font-size: 14px;
  line-height: 23px;
}
a,
button,
input,
label,
select,
textarea {
  touch-action: manipulation;
}
article,
aside,
footer,
header,
hgroup,
main,
nav,
section {
  display: block;
}
textarea,
input[type="text"],
input[type="email"],
input[type="tel"],
input[type="password"] {
  background: #fff;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  display: block;
  border-radius: 3px;
  padding: 5px 45px 5px 12px;
  border: 1px solid #e8e8e8;
  line-height: 26px;
  font-size: 14px;
  font-family: -apple-system, BlinkMacSystemFont, "Roboto", Arial, Verdana,
    Tahoma, Helvetica, sans-serif;
  color: #353d43;
  width: 100%;
  resize: none;
}
textarea {
  max-height: 120px;
}
input[type="checkbox"] {
  display: none;
}
input[type="radio"] {
  display: none;
}
input:disabled {
  background: #f8f8f9;
}
button {
  background: transparent;
  font-size: 14px;
  font-family: -apple-system, BlinkMacSystemFont, "Roboto", Arial, Verdana,
    Tahoma, Helvetica, sans-serif;
  border: 0;
  line-height: 21px;
  cursor: pointer;
}

td,
th {
  text-align: left;
}
table {
  table-layout: fixed;
  border-collapse: separate;
  width: 100%;
  border-spacing: 0;
}
thead th {
  position: sticky;
  z-index: 3;
  top: 0;
  height: 50px;
  padding: 11px 16px;
  border-bottom: 1px solid #e8e8e8;
  background: #fff;
  font-size: 13px;
  line-height: 18px;
  font-weight: 400;
  color: #999;
}

table td,
table th {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  vertical-align: middle;
}
tbody tr {
  cursor: pointer;
}
tbody tr:hover {
  background: #f8f8f9;
}
tbody td {
  height: 45px;
  padding: 0 16px;
  border-bottom: 1px solid #e8e8e8;
  font-size: 13px;
  line-height: 16px;
}

/* АНИМАЦИЯ */
input,
.chat-message-input-button-send,
.chat-user,
.modal-header-menu-button,
.notify,
.auth-link,
.task-create-button-close,
.contact-tab-button,
.modal-deal-user-button,
.modal-deal-string-button,
.modal-deal-step-icon,
.modal-deal-string,
.table-pagination-list > li > a,
.close,
.modal-chat-input-button-send,
.kanban-step-add-button,
.kanban-button-deal-add,
.header-navigation-item,
.sidebar-navigation-item > a,
.button,
input,
button,
tbody tr {
  -webkit-transition: all 0.2s;
  -o-transition: all 0.2s;
  transition: all 0.2s;
}

/* КОНТЕНТ */

.auth,
.service {
  min-height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  background: #eef0f4;
}
.auth-modal,
.service-container {
  margin: 0 auto;
  width: 400px;
}
.auth-container {
  width: 100%;
  background: #fff;
  padding: 30px;
  border-radius: 10px;
  box-shadow: 0 8px 38px 2px rgba(0, 0, 0, 0.07);
}
.auth-title {
  font-size: 21px;
  font-weight: 700;
  margin-bottom: 30px;
}
.auth-string {
  margin-bottom: 10px;
}
.auth-string:last-child {
  margin-bottom: 0;
}
.auth-label {
  font-size: 13px;
  color: #757575;
  margin-bottom: 6px;
}
.auth-button > .button {
  width: 100%;
  line-height: 50px;
}
.auth-row {
  margin-bottom: 30px;
}
.auth-row:last-child {
  margin-bottom: 0;
}
.auth-items {
  display: flex;
  justify-content: center;
  margin: 0 -10px;
  align-items: center;
}
.auth-item {
  padding: 0 10px;
}
.auth-link {
  color: #757575;
}
.auth-link:hover {
  color: #353d43;
}
.auth-logo {
  width: 150px;
  margin-bottom: 30px;
}
.auth-error {
  text-align: center;
  border-radius: 3px;
  color: #d83b3b;
  font-size: 13px;
}
.auth-footer {
  margin-top: 30px;
}

.main {
  width: 100%;
  height: 100%;
}
.main-items {
  display: flex;
  align-items: stretch;
  flex: 1 1;
  min-width: 0;
  min-height: 0;
  max-height: 100%;
  max-width: 100%;
  height: 100%;
  width: 100%;
}
.main-sidebar {
  background: #026fd6;
  position: relative;
  z-index: 10;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  justify-content: space-between;
  height: 100%;
  width: 60px;
}
.main-content {
  display: flex;
  flex: 1 1;
  position: relative;
  flex-direction: column;
  min-width: 0;
  min-height: 0;
  max-width: 100%;
  height: 100%;
  background: #eef0f4;
}

.header {
  display: flex;
  flex: 0 0 auto;
  padding: 0 65px;
  min-height: 58px;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.03);
  background: #fff;
  position: relative;
  z-index: 6;
}

.header-items {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}
.header-inner-items {
  display: flex;
  align-items: center;
  margin: -15px;
}
.header-inner-item {
  padding: 15px;
}
.header-navigation-items {
  margin: 0 -10px;
}
.header-navigation-item {
  display: inline-block;
  text-decoration: none;
  margin: 0 10px;
  color: #757575;
  position: relative;
}
.header-navigation-item:hover {
  color: #353d43;
}

.header-account {
  position: relative;
}
.header-account > .active {
  display: block;
}
.header-account-avatar {
  width: 35px;
  height: 35px;
  border-radius: 50%;
  overflow: hidden;
  cursor: pointer;
}
.header-account-dropdown {
  position: absolute;
  top: 50px;
  right: 0;
  width: 220px;
  z-index: 20;
  display: none;
}
.header-account-dropdown-item {
  display: block;
  padding: 7px 12px;
  color: #757575;
}
.header-account-dropdown-item:hover {
  background: #026fd6;
  color: #fff;
}
.header-account-dropdown-item:last-child {
  border-bottom: 0;
}
.header-account-dropdown-container {
  background: #fff;
  overflow: hidden;
  border-radius: 5px;
  box-shadow: 0 2px 16px 0 rgba(6, 19, 39, 0.09);
}

.button {
  font-size: 14px;
  line-height: 32px;
  padding: 0 20px;
  border-radius: 3px;
  text-align: center;
  display: inline-block;
  color: #fff;
  background: #026fd6;
}
.button:hover {
  background: #037ff3;
}

.button-delete {
  background: #d83b3b;
}
.button-delete:hover {
  background: #f34242;
}

.content {
  height: 100%;
  position: relative;
}
.content-inner {
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  position: absolute;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
  -ms-scroll-chaining: none;
  overscroll-behavior: contain;
}

.content-header {
  position: sticky;
  position: -webkit-sticky;
  left: 0;
  z-index: 4;
  padding: 30px 65px 10px 65px;
}

.contact-task {
  padding: 20px;
  border-bottom: 1px solid #e8e8e8;
}
.contact-task-body {
  position: relative;
}
.contact-task-container {
  padding-left: 35px;
}
.contact-task-title > a {
  color: #353d43;
}

.contact-task-date,
.contact-task-creator {
  color: #757575;
}
.contact-task-checked {
  color: #c7c5c5;
  text-decoration: line-through;
}
.contact-task-checked .contact-task-date,
.contact-task-checked .contact-task-creator {
  color: #c7c5c5;
}

.checkbox,
.radio {
  position: absolute;
  left: 0px;
  top: 7px;
}
.checkbox-button,
.radio-button {
  width: 20px;
  height: 20px;
  cursor: pointer;
}
.checkbox-button:before,
.radio-button:before {
  content: "";
  overflow: hidden;
  display: block;
  border: 1px solid #c9c9c9;
  height: 20px;
  width: 20px;
  position: absolute;
  left: 0;
  top: 0;
  border-radius: 50px;
  background-color: #fff;
}
.checkbox-button:after,
.radio-button:after {
  content: "";
  opacity: 0;
  background: #026fd6;
  height: 20px;
  width: 20px;
  position: absolute;
  left: 0;
  top: 0;
  border-radius: 50px;
}
.checkbox > input[type="checkbox"]:checked + .checkbox-button:after,
.radio > input[type="radio"]:checked + .radio-button:after {
  opacity: 1;
}

.chat {
  padding: 20px 65px 20px 65px;
  flex: 1 1;
  display: flex;
  min-width: 0;
  min-height: 0;
  max-width: 100%;
  height: 100%;
}
.chat-container {
  background: #fff;
  border-radius: 5px;
  max-width: 100%;
  position: relative;
  display: flex;
  flex: 1 1 auto;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.08);
}
.chat-content {
  flex: 1 1;
  overflow: auto;
  position: relative;
  min-height: 0;
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
}
.chat-main {
  flex: 1 1;
  min-height: 0;
  min-width: 0;
  overflow: auto;
  display: flex;
  flex-direction: column;
}
.chat-sidebar {
  width: 320px;
  flex: 0 0 auto;
  border-right: 1px solid #e8e8e8;
  position: relative;
  min-height: 0;
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
}
.chat-header {
  padding: 20px;
}
.chat-header-status {
  color: #a3a3a3;
  font-size: 13px;
}
.chat-title {
  font-size: 19px;
  margin-bottom: 20px;
}
.chat-user {
  display: flex;
  padding: 10px 20px;
}
.chat-user:hover,
.chat-user-list > .chat-user-active {
  background: #f6f7f9;
}
.chat-user-avatar {
  width: 34px;
  height: 34px;
  border-radius: 50%;
  overflow: hidden;
  margin-right: 15px;
  flex-shrink: 0;
}
.chat-user-content {
  flex: 1 1;
  min-width: 0;
}
.chat-user-header {
  display: flex;
  align-items: center;
}
.chat-user-name {
  flex: 1 1;
  color: #353d43;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.chat-user-last-message {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  color: #a3a3a3;
  font-size: 13px;
}
.chat-user-last-body {
  display: flex;
}
.chat-user-last-status {
  display: flex;
  align-items: center;
}
.chat-user-last-status-count {
  display: inline-block;
  padding: 0 5px;
  min-width: 18px;
  height: 18px;
  line-height: 18px;
  font-size: 12px;
  border-radius: 18px;
  color: #fff;
  text-align: center;
  background: #026fd6;
  text-transform: uppercase;
}
.chat-body {
  flex: 1 1;
  overflow: hidden;
  display: flex;
}
.chat-user-list {
  overflow-y: auto;
}
.chat-user-date {
  font-size: 13px;
  color: #a3a3a3;
}
.chat-main-header {
  position: relative;
  z-index: 3;
  min-height: 60px;
  flex: 0 0 auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #e8e8e8;
  padding: 10px 20px;
}
.chat-main-body {
  position: relative;
  flex: 1 1 auto;
  display: flex;
  flex-direction: column;
  min-height: 0;
  overflow: scroll;
  padding: 20px;
}
.chat-main-footer {
  flex: 0 0 auto;
  position: relative;
  z-index: 3;
  border-top: 1px solid #e8e8e8;
  padding: 20px;
}
.chat-message-input-items {
  display: flex;
  align-items: center;
}
.chat-message-input-form {
  flex: 1 1;
  width: auto;
  height: auto;
  padding-right: 20px;
}
.chat-message-input-button-send {
  width: 23px;
  opacity: 0.3;
  display: block;
}
.chat-message-input-button-send:hover {
  opacity: 0.5;
}
.chat-message-input-button-send > svg {
  display: block;
}

.panel-items {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: -5px;
}
.panel-item {
  padding: 5px;
}
.panel-button-items {
  display: flex;
  align-items: center;
  margin: -5px;
}
.panel-button-item {
  padding: 5px;
}
.panel-title {
  font-size: 23px;
  line-height: 32px;
}

.side {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(24, 31, 51, 0.6);
  z-index: 26;
}
.side-content {
  display: flex;
  flex-direction: column;
  height: 100%;
}
.side-container {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  max-width: 100%;
  width: 450px;
  background: #fff;
  box-shadow: 0 0 9px 0 rgba(0, 0, 0, 0.06);
}
.side-header {
  border-bottom: 1px solid #e8e8e8;
  background: #f8f8f9;
}
.side-body {
  overflow: scroll;
  flex: 1 1;
  min-height: 0;
  overflow: auto;
}
.side-footer {
  border-top: 1px solid #e8e8e8;
}
.side-headline {
  padding: 20px;
}
.side-title {
  font-size: 16px;
  margin-bottom: 5px;
}
.side-tab {
  padding: 0 20px;
  margin: 0 -10px;
}
.side-tab > .active {
  background: #fff;
  border-color: #e8e8e8;
}
.side-tab-button {
  padding: 10px;
  color: #757575;
  position: relative;
  border-radius: 5px 5px 0 0;
  border: 1px solid transparent;
  border-bottom: 0;
}
.side-tab > .active:after {
  position: absolute;
  bottom: -1px;
  left: 0;
  width: 100%;
  content: "";
  height: 2px;
  background: #fff;
}
.side-tab-button:hover,
.side-tab > .active {
  color: #353d43;
}
.side-description {
  color: #757575;
  font-size: 13px;
}
.side-row {
  padding: 20px;
}
.side-button {
  margin-bottom: 10px;
}
.side-button:last-child {
  margin-bottom: 0;
}
.side-button > .button {
  width: 100%;
}
.side-string {
  margin-bottom: 10px;
}
.side-string:last-child {
  margin-bottom: 0;
}
.side-label {
  font-size: 13px;
  color: #757575;
  margin-bottom: 6px;
}
.side-button-items {
  display: flex;
  margin: -5px;
}
.side-button-item {
  flex: 1 1;
  padding: 5px;
}
.side-button-item > .button {
  width: 100%;
}

.sidebar-navigation {
  flex: 0 0 auto;
  overflow-x: hidden;
  flex-shrink: 1;
}
.sidebar-navigation-item > a,
.sidebar-logo {
  flex: 1 1 60px;
  position: relative;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}
.sidebar-navigation-item a:hover,
.sidebar-navigation-item > .active {
  background: #037ff3;
}
.sidebar-navigation-icon {
  width: 20px;
}
.sidebar-navigation-icon > svg {
  display: block;
  fill: #fff;
}
.sidebar-logo {
  background: #037ff3;
  margin-bottom: 15px;
  height: 58px;
}
.sidebar-logo > a {
  width: 25px;
}
.sidebar-notify {
  animation: notify infinite 1s linear;
}

.side-attached-inner-items {
  display: flex;
  align-items: center;
}
.side-attached-inner-content {
  flex: 1 1;
}
.side-button-close {
  margin: auto 0 auto 8px;
  flex: 0 0 auto;
  width: 12px;
  height: 12px;
  opacity: 0.3;
}
.side-button-close > svg {
  display: block;
}
.side-button-close:hover {
  opacity: 1;
}

.side-attached-title {
  margin-bottom: 3px;
}

.side-attached-item {
  margin-bottom: 10px;
}
.side-attached-item:last-child {
  margin-bottom: 0;
}
.side-attached-container {
  padding: 10px 12px;
  border: 1px solid #e8e8e8;
  border-radius: 5px;
}
.side-attached-type {
  font-size: 13px;
  color: #757575;
}

.staff-items {
  max-height: 210px;
  overflow: scroll;
  border: 1px solid #e8e8e8;
  border-radius: 5px;
}
.staff-item:last-child .staff-container {
  border-bottom: 0;
}
.staff-container {
  padding: 10px 15px;
  border-bottom: 1px solid #e8e8e8;
}
.staff-inner-items {
  display: flex;
  align-items: center;
  margin: -5px;
}
.staff-inner-avatar,
.staff-inner-content,
.staff-inner-checkbox {
  padding: 5px;
}
.staff-inner-avatar {
  flex: 0 0 auto;
}
.staff-inner-content {
  flex: 1 1;
}
.staff-inner-checkbox > .checkbox,
.staff-inner-checkbox > .radio {
  position: relative;
  top: 0;
}
.staff-avatar {
  width: 35px;
  height: 35px;
  border-radius: 50%;
  overflow: hidden;
}

.setting {
  padding: 0 65px 20px 65px;
}
.setting-container {
  background: #fff;
  position: relative;
  padding: 20px;
  border-radius: 5px;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.08);
}
.setting-label {
  font-size: 13px;
  color: #757575;
  margin-bottom: 6px;
}
.setting-group {
  width: 450px;
  margin-bottom: 20px;
}
.setting-title {
  font-size: 16px;
  margin-bottom: 10px;
}
.setting-attribute {
  padding: 10px 0;
}
.setting-attribute-container {
  padding-left: 40px;
  position: relative;
}
.setting-attribute-container .checkbox {
  top: 4px;
}
.setting-attribute-name {
  cursor: pointer;
  display: block;
}
.setting-attribute-title {
  margin-bottom: 3px;
}
.setting-attribute-status {
  color: #757575;
  font-size: 13px;
}
.setting-row {
  margin-bottom: 20px;
}
.setting-row:last-child {
  margin-bottom: 0px;
}
.setting-string {
  margin-bottom: 10px;
}
.setting-string:last-child {
  margin-bottom: 0;
}

.service-description,
.service-button {
  text-align: center;
  margin-top: 30px;
}

.kanban {
  padding-bottom: 20px;
}
.kanban-step {
  display: table;
  border-collapse: separate;
  border-spacing: 0;
  background: #eef0f4;
  padding: 15px 65px 15px 65px;
  margin-top: -15px;
  position: sticky;
  top: 0;
  z-index: 1;
}
.kanban-step-items {
  display: flex;
  margin: -10px;
}
.kanban-step-item {
  display: flex;
  padding: 10px;
}
.kanban-step-inner-items {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 100%;
}
.kanban-step-button-icon {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  cursor: pointer;
}

.kanban-step-container {
  background: #fff;
  position: relative;
  padding: 10px 20px;
  width: 320px;
  border-radius: 5px;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.08);
}

.kanban-step-add-button {
  display: flex;
  align-items: center;
  position: relative;
  cursor: pointer;
  padding: 10px 20px;
  width: 320px;
  min-height: 71px;
  border-radius: 5px;
}
