    padding: 0;
  }
  .modal-deal-string:hover {
    background: transparent;
  }
  .content-header-items {
    align-items: flex-start;
    flex-direction: column;
  }
  .modal-deal-input > input:hover,
  .modal-deal-title > input:hover {
    background: transparent;
  }
  .modal-deal-input > input:focus,
  .modal-deal-title > input:focus {
    background: transparent;
    border-color: transparent;
  }
  .setting,
  .task {
    padding: 20px 20px 10px 20px;
  }
  .setting-group,
  .modal-header {
    width: 100%;
  }
  .button-closed {
    padding: 0;
  }
  .modal-inner-items {
    padding-top: 35px;
  }
  .modal-chat-body {
    margin-top: 0;
  }
  .modal-employee-wrapper,
  .modal-add-wrapper,
  .modal-attention-wrapper {
    padding: 0 10px;
  }
  .staff-items {
    max-height: 400px;
  }
  .filter-input-search {
    width: 200px;
  }
  .filter-input-search > input {
    padding: 5px 0;
  }
  .filter-input-search > input:focus {
    border-color: transparent;
    background: transparent;
  }
  .chat {
    padding: 20px;
  }
}

.main-container {
  display: flex;
  height: 100vh;
